import React from "react";
import PropTypes from "prop-types";
import { Bar } from "react-chartjs-2";
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend,
} from "chart.js";
import zoomPlugin from 'chartjs-plugin-zoom';

ChartJS.register(CategoryScale, LinearScale, BarElement, Title, Tooltip, Legend,zoomPlugin);

const ChipGraph = ({ graphData, onSegmentDoubleClick }) => {
  const labels = graphData.map(item => item.x); // Get labels from graphData
  const currentData = graphData.map(item => item.y); // Get current values
  
  const data = {
    labels,
    datasets: [
      {
        label: "Current (mA)",
        data: graphData, // Directly use graphData here
        backgroundColor: [
          "rgba(255, 99, 132, 0.2)",
          "rgba(54, 162, 235, 0.2)",
          "rgba(255, 206, 86, 0.2)",
          "rgba(75, 192, 192, 0.2)",
        ],
        borderColor: [
          "rgba(255, 99, 132, 0.2)",
          "rgba(54, 162, 235, 0.2)",
          "rgba(255, 206, 86, 0.2)",
          "rgba(75, 192, 192, 0.2)",
        ],
        borderWidth: 1,
        barPercentage: 1.0,
        categoryPercentage: 1.0,
      },
    ],
  };

  let lastClickTime = 0;

  const options = {
    onClick: function(evt, elements) {
      const currentTime = new Date().getTime();
      if (elements.length > 0 && currentTime - lastClickTime < 300) { // 300 ms for double-click
        const dataIndex = elements[0].index; // Index of the double-clicked element
        console.log('Double click detected on index:', dataIndex);
        if (onSegmentDoubleClick) {
          onSegmentDoubleClick(dataIndex);
        }
      }
      lastClickTime = currentTime;
    },
    scales: {
      x: {
        title: {
          display: true,
          text: "Time (ms)",
        },
      },
      y: {
        title: {
          display: true,
          text: "Current (mA)",
        },
      },
    },
    plugins: {
      tooltip: {
        callbacks: {
          label: function (context) {
            let label = context.dataset.label || "";
            if (label) {
              label += ": " + context.parsed.y + " mA";
            }
            if (context.raw.startUp) {
              label += "\nStart up: " + context.raw.startUp;
            }
            if (context.raw.length) {
              label += "\nLength: " + context.raw.length;
            }
            if (context.raw.charge) {
              label += "\nCharge: " + context.raw.charge;
            }
            return label;
          },
        },
      },
      legend: {
        display: false,
      },
      zoom: {
        zoom: {
          // wheel: {
          //   enabled: true, // Enable zooming with mouse wheel
          // },
          pinch: {
            enabled: true, // Enable zooming with pinch gesture
          },
          mode: 'xy', // Enable zooming along both axes
        },
        // pan: {
        //   enabled: true, // Enable panning
        //   mode: 'xy', // Enable panning along both axes
        // }
      }
    },
    responsive: true,
    maintainAspectRatio: true,
  };

  return (
    <div style={{ height: "400px", width: "100%", overflow: "auto" }}>
      <Bar data={data} options={options} />
    </div>
  );
};

// PropTypes validation (optional but recommended for debugging purposes)
ChipGraph.propTypes = {
  graphData: PropTypes.arrayOf(
    PropTypes.shape({
      x: PropTypes.string.isRequired,
      y: PropTypes.number.isRequired,
      startUp: PropTypes.string,
      length: PropTypes.string,
      charge: PropTypes.string,
    })
  ).isRequired,
};

export default ChipGraph;
