import React, { useState, useEffect, useContext } from "react";
import "./ChipDetails.css";
import StateContext from "./StateContext";

const ChipDetails = ({ chipDetail }) => {
  if (!chipDetail) {
    chipDetail = {
      chip: "Loading...",
      voltage: "Loading...",
      interval: "Loading...",
      length: "Loading...",
      onDataRate: "Loading...",
      bleEventCharge: "Loading...",
      lfclock: "Loading...",
      idleCurrent: "Loading...",
      totalAverageCurrent: "Loading...",
    };
  }
  const { formState } = useContext(StateContext);
  const isConnectionRole = formState.role.includes("Connection");
  return (
    <div className="chip-details-container">
      <div className="column">
        <h2 className="detailheader">Test setup</h2>
        <div className="detailRow">
          <strong>Chip:</strong> {formState.chip}
        </div>
        <div className="detailRow">
          <strong>Voltage:</strong> {formState.voltage} V
        </div>

        <h2 className="detailheader">BLE event details</h2>
        <div className="detailRow">
          <strong>Interval:</strong>{" "}
          {parseInt(formState.advertisinginterval, 10) + 5} ms
        </div>
        <div className="detailRow">
          <strong>Length:</strong> {chipDetail.length} ms
        </div>
        {isConnectionRole && (
          <>
            <div className="detailRow">
              <strong>Number of packets:</strong>{" "}
              1
            </div>
            <div className="detailRow">
              <strong>Master sleep clock accuracy:</strong>{" "}
              20ppm
            </div>
            <div className="detailRow">
              <strong>Slave sleep clock accuracy:</strong>{" "}
              20ppm
            </div>
          </>
        )}

        <h2 className="detailheader">Data transmission</h2>
        {isConnectionRole && (
          <>
            <div className="detailRow">
              <strong>LL PDU Size:</strong>{" "}
              27 Byte
            </div>
            <div className="detailRow">
              <strong>TX payload per event:</strong>{" "}
              22 Byte
            </div>
            <div className="detailRow">
              <strong>TX LL throughput:</strong>{" "}
              99 bit/s
            </div>
            <div className="detailRow">
              <strong>RX payload per event:</strong>{" "}
              0 Byte
            </div>
            <div className="detailRow">
              <strong>RX LL throughput:</strong>{" "}
              0 bit/s
            </div>
          </>
        )}
        <div className="detailRow">
          <strong>On air data rate:</strong> 1 Mbps
        </div>
      </div>
      <div className="column">
        <h2 className="detailheader">Current consumption</h2>
        <div className="detailRow">
          <strong>BLE event total charge:</strong> {chipDetail.bleEventCharge}{" "}
          C
        </div>
        {formState.lfclock === "internalrc" && (
          <>
            <div className="detailRow">
              <strong>LF clock calibration current:</strong>{" "}
              {chipDetail.lfclock} C
            </div>
          </>
        )}
        <div className="detailRow">
          <strong>Idle current:</strong> {chipDetail.idleCurrent} A
        </div>
        <div className="detailRow">
          <strong>Total average current:</strong>{" "}
          {chipDetail.totalAverageCurrent} mA
        </div>
      </div>
    </div>
  );
};

export default ChipDetails;
