import React, { useState, useEffect, useContext } from "react";
import "./Navbar.css";
import StateContext from "./StateContext";
import { adjustData, radioTxPowerToScalingFactor } from "./graphUtils"; // Import your utilities

const NavBar = ({ onGraphDataFetch, onChipDetailChange }) => {
  const contextValue = useContext(StateContext);
  const { formState, setFormState } = contextValue;
  const [graphData, setGraphData] = useState([]);
  const [chipDetail, setChipDetail] = useState([]);
  const [basegraphdata, setbasegraphdata] = useState([]);
  const isAdvertisingRole = formState.role.includes("Advertising");
  const isConnectionRole = formState.role.includes("Connection");
  const [tooltip, setTooltip] = useState({
    show: false,
    content: "",
    x: 0,
    y: 0,
  });
  const txPayloadMax = formState.extendedAdvertising ? 255 : 31;

  const baseUrl = `${window.location.protocol}//${window.location.hostname}:3001`;


  useEffect(() => {
    if (
      formState.lastChangedField === "chip" ||
      formState.lastChangedField === "role" ||
      formState.lastChangedField === "extendedAdvertising"
    ) {
      console.log("fetch!");
      fetchData();
      fetchchipdata();
    } else {
      console.log("Local!");
      adjustGraphDataLocally();
    }
  }, [formState]); // This will fetch data on component mount and whenever formState changes

  const adjustGraphDataLocally = () => {
    const scalingFactor =
      radioTxPowerToScalingFactor[formState.radiotxpower] || 1; // Default to 1 if not found

    // Adjust the y values for Radiotx points
    const adjustedData = basegraphdata.map((dataPoint) => {
      let modifiedDataPoint = { ...dataPoint };
      // Adjust y values for Radiotx points based on scaling factor
      if (dataPoint.x === "Radiotx") {
        modifiedDataPoint.y = parseFloat(
          (dataPoint.y * scalingFactor).toFixed(1)
        );
      }

      if (dataPoint.x === "Radiotx" && formState.txpayload) {
        const intervalAdjustment = formState.txpayload * 8 + 128;
        modifiedDataPoint.length = parseFloat(intervalAdjustment).toFixed(1);
      }

      let lengthAdjustment = 0;

      // Adjustments for 'Window Widening' based on connection interval
      if (dataPoint.x === "Window Widening" && formState.connectioninterval) {
        lengthAdjustment += (formState.connectioninterval - 100) * 1.04;
      }

      // Additional adjustment for 'Window Widening' based on slave latency
      if (dataPoint.x === "Window Widening" && formState.slavelatency) {
        lengthAdjustment += formState.slavelatency * 4;
      }

      // Apply total length adjustment if any adjustments are computed
      if (lengthAdjustment !== 0) {
        modifiedDataPoint.length = parseFloat(
          (parseFloat(dataPoint.length) + lengthAdjustment).toFixed(1)
        );
      }

      return modifiedDataPoint;
    });
    console.log(adjustedData);
    setGraphData(adjustedData);
    onGraphDataFetch(adjustedData);
  };

  const adjustData = (data) => {
    return data.map((dataPoint) => {
      let modifiedDataPoint = { ...dataPoint };
      const scalingFactor =
        radioTxPowerToScalingFactor[formState.radiotxpower] || 1;

      // Adjust 'Radiotx' points based on scaling factor
      if (dataPoint.x === "Radiotx") {
        modifiedDataPoint.y = parseFloat(
          (dataPoint.y * scalingFactor).toFixed(2)
        );
      }

      if (dataPoint.x === "Radiotx" && formState.txpayload) {
        const intervalAdjustment = formState.txpayload * 7.925 + 131.179;
        modifiedDataPoint.length = parseFloat(intervalAdjustment).toFixed(1);
      }

      let lengthAdjustment = 0;

      // Adjustments for 'Window Widening' based on connection interval
      if (dataPoint.x === "Window Widening" && formState.connectioninterval) {
        lengthAdjustment += (formState.connectioninterval - 100) * 1.04;
      }

      // Additional adjustment for 'Window Widening' based on slave latency
      if (dataPoint.x === "Window Widening" && formState.slavelatency) {
        lengthAdjustment += formState.slavelatency * 4;
      }

      // Apply total length adjustment if any adjustments are computed
      if (lengthAdjustment !== 0) {
        modifiedDataPoint.length = parseFloat(
          (parseFloat(dataPoint.length) + lengthAdjustment).toFixed(1)
        );
      }

      return modifiedDataPoint;
    });
  };

  const radioTxPowerToScalingFactor = {
    "0dBm": 1,
    "9dBm": 1.26,
    "18dBm": 1.55,
    "21dBm": 3.2
  };

  const chipOptions = {
    'ESP32-C3': ['0dBm', '9dBm', '21dBm'],
    'ESP32-C6': ['0dBm', '9dBm', '18dBm'],
    'ESP32-H2': ['0dBm', '9dBm', '18dBm']
  };

  const fetchData = async () => {
    try {
      const response = await fetch(
        "https://flyioserver.fly.dev/api/get-graph-data",
        {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify(formState), // Assuming formState contains your form data
        }
      );
      if (!response.ok) {
        throw new Error("Network response was not ok");
      }

      const data = await response.json();
      const adjustedData = adjustData(data);
      console.log(adjustedData);
      setGraphData(adjustedData);
      onGraphDataFetch(adjustedData);
      setbasegraphdata(data);
    } catch (error) {
      console.error("Failed to fetch:", error);
    }
  };
  //http://localhost:3001
  const fetchchipdata = async () => {
    try {
      const response = await fetch("https://flyioserver.fly.dev/api/chip-details", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(formState), // Assuming formState contains your form data
      });
      if (!response.ok) {
        throw new Error("Network response was not ok");
      }

      const data = await response.json();
      console.log(data);
      setChipDetail(data);
      onChipDetailChange(data);
    } catch (error) {
      console.error("Failed to fetch:", error);
    }
  };

  const showTooltip = (e, htmlContent) => {
    setTooltip({
      show: true,
      content: htmlContent,
      x: e.pageX, // For simplicity, positioning based on page coordinates
      y: e.pageY,
    });
  };

  const chips = [
    "ESP32-C3",
    "ESP32-C2",
    "ESP32-C6",
    "ESP32-H2",
  ];

  const handleChange = (e) => {
    const { name, value, type, checked } = e.target;
    if (name === "voltage") {
      const newValue =
        type === "range" ? parseInt(value, 10) / 10 : parseFloat(value);
      setFormState((prevState) => ({
        ...prevState,
        [name]: newValue,
        lastChangedField: name,
      }));
    } else {
      setFormState((prevState) => ({
        ...prevState,
        [name]: type === "checkbox" ? checked : value,
        lastChangedField: name,
      }));
    }
  };

  const handleBlur = (e) => {
    const { name, value } = e.target;
    let newValue = parseFloat(value);
    if (name === "voltage") {
      if (newValue < 1.7) newValue = 1.7;
      if (newValue > 3.6) newValue = 3.6;
      setFormState((prevState) => ({
        ...prevState,
        [name]: newValue.toFixed(1),
        lastChangedField: name,
      }));
    }
    if (name === "advertisinginterval") {
      if (newValue < 20) newValue = 20;
      if (newValue > 10240) newValue = 10240;
      setFormState((prevState) => ({
        ...prevState,
        [name]: newValue,
        lastChangedField: name,
      }));
    }
  };

  const getOptionsForChipType = (chipType) => {
    const options = chipOptions[chipType] || ['0dBm', '9dBm', '18dBm'];
    return options.map(option => <option key={option} value={option}>{option}</option>);
  };

  return (
    <div className="navbar">
      <div className="settings-container">
        <h2 className="settings-header">Chip Settings</h2>
        <div className="settings-group">
          <label>Chip:</label>
          <select name="chip" value={formState.chip} onChange={handleChange}>
            <option value="">Select a Chip</option>
            {chips.map((chip) => (
              <option key={chip} value={chip}>
                {chip}
              </option>
            ))}
          </select>
        </div>
        <div className="settings-group">
          <label>
            Voltage:
            <input
              type="number"
              name="voltage"
              value={formState.voltage} // Dividing by 10 to get the actual voltage
              onChange={handleChange}
              onBlur={handleBlur} // Validate on blur
              min="1.7" // 1.7V minimum
              max="3.6" // 3.6V maximum
              step="0.1" // Step in tenths of a volt
            />{" "}
            V
          </label>

          <input
            type="range"
            min={17}
            max={36}
            name="voltage"
            value={formState.voltage * 10}
            onChange={handleChange}
            step={1}
          />
        </div>
        <div className="settings-group">
          <label>LF clock: </label>
          <select
            name="lfclock"
            value={formState.lfclock}
            onChange={handleChange}
          >
            <option value="externalcrystal">External crystal</option>
            <option value="internalrc">Internal RC</option>
          </select>
        </div>
        <div className="settings-group">
          <label>Radio TX power: </label>
          <select
            name="radiotxpower"
            value={formState.radiotxpower}
            onChange={handleChange}
          >
            {getOptionsForChipType(formState.chip)}
          </select>
        </div>
      </div>
      <div className="settings-container">
        <h2 className="settings-header">BLE settings</h2>
        <div className="settings-group">
          <label>
            Role:
            <span
              onMouseEnter={(e) =>
                showTooltip(
                  e,
                  "Advertising (TX/RX): <br> - Connectable <br> - Connectable, non-scannable <br> - Non-connectable, scannable <br><br> Advertising (TX only): <br> - Non-connectable, non-scannable"
                )
              }
              onMouseLeave={() => setTooltip({ ...tooltip, show: false })}
            >
              {" "}
              
            </span>
          </label>
          <select name="role" value={formState.role} onChange={handleChange}>
            <option value="Advertising(TX/RX)">Advertising (TX/RX)</option>
            <option value="Advertising(TX only)">Advertising (TX only)</option>
            <option value="Connection(peripheral)">
              Connection (peripheral)
            </option>
            <option value="Connection(central)">Connection (central)</option>
          </select>
        </div>
        {/* Tooltip div */}
        {tooltip.show && (
          <div
            className="tooltip"
            style={{ top: tooltip.y + "px", left: tooltip.x + "px" }}
            dangerouslySetInnerHTML={{ __html: tooltip.content }}
          />
        )}
        {isAdvertisingRole && (
          <>
            <div className="settings-group toggle-switch">
              <label>Extended Advertising:</label>
              <div className="switch">
                <input
                  type="checkbox"
                  name="extendedAdvertising"
                  checked={formState.extendedAdvertising}
                  value={formState.extendedAdvertising}
                  onChange={handleChange}
                  id="extendedAdvertising"
                />
                <label
                  htmlFor="extendedAdvertising"
                  className="slider round"
                ></label>
              </div>
            </div>
            <div className="settings-group">
              <label>
                Advertising Interval (ms):{" "}
                <input
                  type="number"
                  name="advertisinginterval"
                  value={formState.advertisinginterval}
                  onChange={handleChange}
                  onBlur={handleBlur}
                  step="50"
                  min="20"
                  max="10240"
                />
              </label>
              <input
                type="range"
                min="20"
                max="10240"
                name="advertisinginterval"
                value={formState.advertisinginterval}
                onChange={handleChange}
                step="50"
              />
            </div>
            <div className="settings-group">
              <label>
                TX Payload (Bytes):{" "}
                <input
                  type="number"
                  min="0"
                  max={txPayloadMax}
                  name="txpayload"
                  value={formState.txpayload}
                  onChange={handleChange}
                />
              </label>
              <input
                type="range"
                min="0"
                max={txPayloadMax}
                name="txpayload"
                value={formState.txpayload}
                onChange={handleChange}
              />
            </div>
          </>
        )}
        {isConnectionRole && (
          <>
            <div className="settings-group">
              <label>
                Connection Interval (ms): {formState.connectioninterval}
              </label>
              <input
                type="range"
                min="20"
                max="4000"
                name="connectioninterval"
                value={formState.connectioninterval}
                onChange={handleChange}
              />
            </div>
            <div className="settings-group">
              <label>TX Payload per Event (Byte):</label>
              <input
                type="number"
                min="0"
                max="216"
                name="txpayloadperevent"
                value={formState.txpayloadperevent}
                onChange={handleChange}
              />
            </div>
            <div className="settings-group">
              <label>RX Payload per Event (Byte):</label>
              <input
                type="number"
                min="0"
                max="216"
                name="rxpayloadperevent"
                value={formState.rxpayloadperevent}
                onChange={handleChange}
              />
            </div>
            <div className="settings-group toggle-switch">
              <label>Data Packet Length Extension:</label>
              <div className="switch">
                <input
                  type="checkbox"
                  name="datapacketlengthextension"
                  checked={formState.datapacketlengthextension}
                  value={formState.datapacketlengthextension}
                  onChange={handleChange}
                  id="datapacketlengthextension"
                />
                <label
                  htmlFor="datapacketlengthextension"
                  className="slider round"
                ></label>
              </div>
            </div>
            <div className="settings-group toggle-switch">
              <label>Connection Event Length Extension:</label>
              <div className="switch">
                <input
                  type="checkbox"
                  name="connectioneventlengthextension"
                  checked={formState.connectioneventlengthextension}
                  value={formState.connectioneventlengthextension}
                  onChange={handleChange}
                  id="connectioneventlengthextension"
                />
                <label
                  htmlFor="connectioneventlengthextension"
                  className="slider round"
                ></label>
              </div>
            </div>
            <h2 className="settings-header">Advanced</h2>
            <div className="settings-group">
              <label>PHY:</label>
              <select name="phy" value={formState.phy} onChange={handleChange}>
                <option value="1 Mbps">1 Mbps</option>
                <option value="2 Mbps">2 Mbps</option>
              </select>
            </div>
            {formState.datapacketlengthextension === true && (
              <>
                <div className="settings-group">
                  <label>LL PDU size: {formState.llpdusize}</label>
                  <input
                    type="range"
                    min="27"
                    max="251"
                    name="llpdusize"
                    value={formState.llpdusize}
                    onChange={handleChange}
                  />
                </div>
              </>
            )}
            <div className="settings-group">
              <label>Slave Latency: {formState.slavelatency}</label>
              <input
                type="range"
                min="0"
                max="159"
                name="slavelatency"
                value={formState.slavelatency}
                onChange={handleChange}
              />
            </div>
            <div className="settings-group">
              <label>
                Slave Sleep Clock Accuracy (ppm):{" "}
                {formState.slavesleepclockaccuracy}
              </label>
              <input
                type="range"
                min="20"
                max="500"
                name="slavesleepclockaccuracy"
                value={formState.slavesleepclockaccuracy}
                onChange={handleChange}
              />
            </div>
          </>
        )}
      </div>
    </div>
  );
};

export default NavBar;
