import React, { useEffect, useState, useContext } from 'react';
import Plot from 'react-plotly.js';
import * as d3 from 'd3';
import StateContext from "./StateContext";
import './InteractivePlot.css'

const InteractivePlot = () => {
    const { formState } = useContext(StateContext);
    const [data, setData] = useState({
        x: [],
        y: []
    });
    const [isFullScreen, setIsFullScreen] = useState(false);
    const [layout, setLayout] = useState({
        xaxis: {
            title: 'Time (ms)',
            titlefont: {
                family: 'Arial, sans-serif',
                size: 14,
                color: 'grey'
            },
            tickfont: {
                family: 'Old Standard TT, serif',
                size: 14,
                color: 'black'
            }
        },
        yaxis: {
            title: 'Current (mA)',
            titlefont: {
                family: 'Arial, sans-serif',
                size: 14,
                color: 'grey'
            },
            tickfont: {
                family: 'Old Standard TT, serif',
                size: 14,
                color: 'black'
            },
            fixedrange: true
        },
        plot_bgcolor: '#f3f3f3',  // Light grey background
        paper_bgcolor: '#fff',  // White paper background
        margin: { t: 40},  // Reducing the top margin
        width: "100%"
    });

    const config = {
        scrollZoom: true,  // Enabling scroll zoom
        responsive: true, // Ensure responsiveness of the plot
        displaylogo: false,
        displayModeBar: true,
    };

    useEffect(() => {
        const currentThreshold = 0.01;
        let originalInterval =100;
        const stretchFactor = (formState.advertisinginterval - originalInterval) / originalInterval;
        let additionalTime = 0;
        const rowConverter = (d, index) => {
            const value = parseFloat(Object.values(d)[0]);
            // Here we modify the x value to be index * 0.1 to scale it as 0.1 ms per increment
            return { x: index/20, y: value * 1000 };  // Convert the y value to milliamps
        };

        const adjustDataForInterval = (loadedData) => {
            let adjustedData = [];
            let isBelowThreshold = false;
    
            loadedData.forEach((point, index) => {
                // Determine if current point is below the threshold
                if (point.y < currentThreshold) {
                    if (!isBelowThreshold) {
                        // Just entered a new below-threshold segment
                        isBelowThreshold = true;
                        // Begin this segment with the current x value
                        point.x = index * 0.05 + additionalTime;
                    } else {
                        // We are in a below-threshold segment, increase the additional time
                        additionalTime += stretchFactor * 1.25;  // Stretch by the stretch factor
                        point.x = index * 0.05 + additionalTime;
                    }
                } else {
                    if (isBelowThreshold) {
                        // Just exited a below-threshold segment
                        isBelowThreshold = false;
                    }
                    // Above-threshold point, assign x normally, accounting for any additional time added
                    point.x = index * 0.05 + additionalTime;
                }
                adjustedData.push(point);
            });
    
            return adjustedData;
        };

        d3.csv("truncate.csv", rowConverter).then((loadedData) => {
            const adjustedData = adjustDataForInterval(loadedData, formState.advertisinginterval);
            setData({
                x: adjustedData.map(d => d.x),  // Set x values from loadedData
                y: adjustedData.map(d => d.y)   // Set y values from loadedData
            });
        }).catch(error => {
            console.error('Error loading the CSV file: ', error);
        });

        setLayout(prevLayout => ({
            ...prevLayout,
            paper_bgcolor: "rgb(235, 245, 255,0.2)", // Adjust the color to match your theme
            plot_bgcolor: '#fafafa',  // Changed to a very light grey
            width: isFullScreen ? window.innerWidth-20 : 900,
            height: isFullScreen ? window.innerHeight-60 : 540,
            autosize: true,
            margin:{r:15}  
        }));
    }, [formState.advertisinginterval, isFullScreen]);

    const handleFullScreen = () => {
        setIsFullScreen(!isFullScreen);
    };

    const plotClasses = isFullScreen ? "fullscreen" : "";

    useEffect(() => {
        const handleKeyDown = (event) => {
            if (event.key === "Escape" && isFullScreen) {
                setIsFullScreen(false);  // Exit full screen mode
            }
        };
        // Add event listener when component mounts
        window.addEventListener('keydown', handleKeyDown);
        // Clean up event listener when component unmounts
        return () => window.removeEventListener('keydown', handleKeyDown);
    }, [isFullScreen]); 

    return (
        <div className={isFullScreen ? "fullscreen" : ""}>
            <Plot
                data={[
                    {
                        x: data.x,
                        y: data.y,
                        type: 'scatter',
                        mode: 'lines',
                        line: {
                            color: 'orange',
                            width: 2
                        }
                    }
                ]}
                layout={layout}
                config={config}
            />
            <div className="button-container">
                <button className="fullscreen-button" onClick={handleFullScreen}>
                    {isFullScreen ? 'Exit Full Screen' : 'Full Screen'}
                </button>
            </div>
        </div>
    );
};

export default InteractivePlot;
