import React, { useEffect, useRef } from 'react';
import * as d3 from 'd3';
import './BarChartWithD3.css';

const BarChartWithD3 = ({ data }) => {
  const d3Container = useRef(null);
  const legendContainerRef = useRef(null);

  useEffect(() => {
    if (data && d3Container.current) {
      const svg = d3.select(d3Container.current);

      const width = +svg.attr('width');
      const height = +svg.attr('height');
      const margin = { top: 20, right: 20, bottom: 60, left: 60 }; // Adjusted for axis labels

      const usToMs = 1 / 1000;
      const totalLengthMs = d3.sum(data, d => parseFloat(d.length) * usToMs);

      const xScale = d3.scaleLinear().range([margin.left, width - margin.right]);
      const yScale = d3.scaleLinear().range([height - margin.bottom, margin.top]);

      xScale.domain([0, totalLengthMs]);
      yScale.domain([0, d3.max(data, d => d.y)]);

      const customColors = [
        'rgba(255, 99, 132, 0.3)',  // Pink with transparency
        'rgba(54, 162, 235, 0.3)',  // Blue with transparency
        'rgba(255, 206, 86, 0.3)',  // Yellow with transparency
        'rgba(75, 192, 192, 0.3)',  // Green with transparency
        'rgba(153, 102, 255, 0.3)', // Purple with transparency
        'rgba(255, 159, 64, 0.3)'   // Orange with transparency
      ];

      const labels = data.map(d => d.x); // Extract the labels
      const uniqueLabels = [...new Set(labels)]; // Ensure uniqueness
      const colorScale = d3.scaleOrdinal()
                           .domain(uniqueLabels)
                           .range(customColors); // Use a D3 color scheme

     


      svg.selectAll("*").remove();

      const makeXGridlines = () => d3.axisBottom(xScale).ticks(5);
      const makeYGridlines = () => d3.axisLeft(yScale).ticks(5);

      svg.append("g")
        .attr("class", "grid")
        .attr("transform", `translate(0,${height - margin.bottom})`)
        .call(makeXGridlines()
          .tickSize(-height + margin.top + margin.bottom)
          .tickFormat("")
        )
        .call(g => g.select(".domain").remove())
        .call(g => g.selectAll(".tick line").attr("stroke-opacity", 0.2).attr("stroke-dasharray", "2,2"));

      // Add the Y gridlines
      svg.append("g")
        .attr("class", "grid")
        .attr("transform", `translate(${margin.left},0)`)
        .call(makeYGridlines()
          .tickSize(-width + margin.left + margin.right)
          .tickFormat("")
        )
        .call(g => g.select(".domain").remove())
        .call(g => g.selectAll(".tick line").attr("stroke-opacity", 0.2).attr("stroke-dasharray", "2,2"));

      const tooltip = d3.select("body").append("div")
        .attr("class", "d3-tooltip")
        .style("opacity", 0);

      let xOffset = margin.left;

      svg.selectAll('.bar')
        .data(data)
        .enter().append('rect')
        .attr('class', 'bar')
        .attr('x', d => {
          const x = xOffset;
          const widthMs = parseFloat(d.length) * usToMs;
          xOffset += xScale(widthMs) - margin.left;
          return x;
        })
        .attr('y', d => yScale(d.y))
        // Use converted length for bar width
        .attr('width', d => xScale(parseFloat(d.length) * usToMs) - margin.left)
        .attr('height', d => height - margin.bottom - yScale(d.y))
        .attr('fill', d => colorScale(d.x)) // Assign color based on label
        .attr('stroke', 'rgba(11, 11, 11, 0.2)') // Adding border to the bars
        .attr('stroke-width', 1.5) // Adjust the border width as needed
        .on('mouseover', function(event, d) {
          tooltip.transition()
            .duration(200)
            .style("opacity", .9);
          tooltip.html(`${d.x}<br/>Current: ${parseFloat(d.y).toFixed(1)}mA<br/>Length: ${d.length}s`)
            .style("background", colorScale(d.x)) // Use colorScale to match bar color
            .style("color", "#000") // Ensure text is readable, assuming dark colors for bars
            .style("padding", "10px") // Add some padding for aesthetics
            .style("border-radius", "8px") // Optional: round corners for the tooltip
            .style("left", (event.pageX) + "px")
            .style("top", (event.pageY - 28) + "px");
        })
        .on('mouseout', function(d) {
          tooltip.transition()
            .duration(500)
            .style("opacity", 0);
        });

      svg.append("g")
        .attr("transform", `translate(0,${height - margin.bottom})`)
        .call(d3.axisBottom(xScale))
        .append("text") // X-axis Label
        .attr("class", "axis-label")
        .attr("y", margin.bottom / 2)
        .attr("x", width / 2)
        .attr("fill", "black")
        .text("Time (ms)");

      svg.append("g")
        .attr("transform", `translate(${margin.left},0)`)
        .call(d3.axisLeft(yScale))
        .append("text") // Y-axis Label
        .attr("class", "axis-label")
        .attr("transform", "rotate(-90)")
        .attr("y", -margin.left + 20)
        .attr("x", -height / 2 + margin.top)
        .attr("fill", "black")
        .text("Current (mA)");

    }
  }, [data]);

  const uniqueLabels = [...new Set(data.map(d => d.x))];
  const customColors = [
    'rgba(255, 99, 132, 0.3)',
    'rgba(54, 162, 235, 0.3)',
    'rgba(255, 206, 86, 0.3)',
    'rgba(75, 192, 192, 0.3)',
    'rgba(153, 102, 255, 0.3)',
    'rgba(255, 159, 64, 0.3)'
  ];
  const colorScale = d3.scaleOrdinal()
                       .domain(uniqueLabels)
                       .range(customColors);

  return (
    <div style={{ height: "480px", width: "810px", overflow: "auto" }}>
      <svg
        className="d3-component"
        width={800}
        height={400}
        ref={d3Container}
      />
       <div ref={legendContainerRef} className="legend-container" style={{ marginTop: '10px', display: 'flex', flexWrap: 'wrap', justifyContent: 'center' }}>
        {uniqueLabels.map((label, index) => (
          <div key={label} style={{ display: 'flex', alignItems: 'center', marginRight: '20px',marginBottom: '10px' }}>
            <span style={{ display: 'inline-block', width: '20px', height: '20px', backgroundColor: colorScale(label), marginRight: '5px', border: '1px solid rgba(0,0,0,0.2)' }}></span>
            <span>{label}</span>
          </div>
        ))}
      </div>
    </div>
  );
};

export default BarChartWithD3;
