import React, { useState } from "react";
import Plot from "react-plotly.js";

const ChipDetailsIndicators = ({ chipDetail,prevChipDetail }) => {
  const chipChanged = chipDetail?.chip|| 0 !== prevChipDetail?.chip|| 0;
  const layout = {
    width: 800,
    height: 200,
    margin: { t: 50, b: 50, l: 20, r: 20 },
    grid: { rows: 1, columns: 4, pattern: "independent" },
    font: { size: 12 }, // Adjusted for better fitting in smaller blocks
    paper_bgcolor: "rgb(235, 245, 255,0.2)", // Adjust the color to match your theme
    plot_bgcolor: "#f4f4f8", // Same as above to maintain consistency
    template: {
      data: {
        indicator: [
          {
            title: { text: "Details", font: { size: 12 } }, // Title font size
            mode: "delta",
          },
        ],
      },
    },
  };

  const data = [
    {
      type: 'indicator',
      mode: 'delta',
      value: chipChanged ? chipDetail?.length || 0 : 0,
      delta: { reference: prevChipDetail?.length || 0 },
      title: { text: 'Length' },
      domain: { row: 0, column: 0 }
    },
    {
      type: 'indicator',
      mode: 'delta',
      value: chipChanged ? chipDetail?.bleEventCharge || 0 : 0,
      delta: { reference: prevChipDetail?.bleEventCharge || 0},
      title: { text: 'BLE Event Charge' },
      domain: { row: 0, column: 1 }
    },
    {
      type: 'indicator',
      mode: 'delta',
      value: chipChanged ? chipDetail?.idleCurrent || 0 : 0,
      delta: { reference: prevChipDetail?.idleCurrent || 0 },
      title: { text: 'Idle Current' },
      domain: { row: 0, column: 2 }
    },
    {
      type: 'indicator',
      mode: 'delta',
      value: chipChanged ? chipDetail?.totalAverageCurrent || 0 : 0,
      delta: { reference: prevChipDetail?.totalAverageCurrent || 0},
      title: { text: 'Total Average Current' },
      domain: { row: 0, column: 3 }
    },
  ];

  return (
    <Plot
      data={data}
      layout={layout}
      style={{ width: "100%", height: "100%" }}
    />
  );
};

export default ChipDetailsIndicators;
