import React, { useState, useEffect } from "react";
import Navbar from "./components/Navbar";
import ChipDetails from "./components/ChipDetails";
import ChipGraph from "./components/ChipGraph";
import "./App.css";
import espressiflogo from "/Users/joshuagoh/Desktop/esp-ble-profiler/src/assets/images/680a940e-48c9-48f9-a27d-96aa52a5e076.png";
import espressiflogo1 from "/Users/joshuagoh/Desktop/esp-ble-profiler/src/assets/images/logo-black.svg";
import BarChartWithD3 from "./components/BarChartWithD3";
import LineChartWithChartJS from "./components/LineChartWithChartJS";
import StateContext from "./components/StateContext";
import LineChartWithD3 from "./components/LineChartWithD3";
import InteractivePlot from "./components/InteractivePlot";
import useKonamiCode from "./components/useKonamiCode";
import KonamiGif from "/Users/joshuagoh/Desktop/esp-ble-profiler/src/assets/images/funny-my-favorite-dog-gifs-5.gif";
import ChipDetailsIndicators from "./components/ChipDetailsIndicators";

function App() {
  const [graphData, setGraphData] = useState([]);
  const [chipDetail, setChipDetail] = useState(null);
  const [isDetailedView, setIsDetailedView] = useState(false);
  const [detailedGraphData, setDetailedGraphData] = useState([]); // For the detailed graph
  const [prevChipDetail, setPrevChipDetail] = useState(null);

  const [formState, setFormState] = useState({
    chip: "",
    voltage: 3.3,
    lfclock: "External crystal",
    radiotxpower: "3dBm",
    role: "Advertising(TX/RX)",
    extendedAdvertising: false,
    advertisinginterval: 53,
    txpayload: 0,
    connectioninterval: 100,
    txpayloadperevent: 0,
    rxpayloadperevent: 0,
    datapacketlengthextension: false,
    connectioneventlengthextension: false,
    phy: "1 Mbps",
    llpdusize: 251,
    slavelatency: 0,
    slavesleepclockaccuracy: 20,
    lastChangedField: null, // Track the last changed field
  });

  const handleGraphDataFetch = (data) => {
    setGraphData(data);
  };
  const handleChipDetailChange = (newDetail) => {
    if (chipDetail) {
      // Ensure there's an existing detail to set as previous
      setPrevChipDetail(chipDetail);
    }
    setChipDetail(newDetail);
  };

  const toggleGraphView = async (dataIndex) => {
    if (isDetailedView) {
      // If currently viewing detailed graph, switch back to normal view
      setIsDetailedView(false);
    } else {
      // If currently viewing normal graph, fetch detailed data and switch to detailed view
      try {
        const response = await fetch(
          "http://localhost:3001/api/get-detailed-graph-data",
          {
            method: "POST",
            headers: { "Content-Type": "application/json" },
            body: JSON.stringify({ dataIndex }), // Customize based on backend needs
          }
        );
        if (!response.ok) {
          throw new Error("Network response was not ok");
        }
        const detailedData = await response.json();
        setDetailedGraphData(detailedData);
        setIsDetailedView(true); // Switch to detailed view
      } catch (error) {
        console.error("Failed to fetch detailed data:", error);
      }
    }
  };

  const [showKonamiGif, setShowKonamiGif] = useState(false);

  const triggerKonamiSurprise = () => {
    console.log("hehe you found my secret XD");
    setShowKonamiGif(true);
    setTimeout(() => setShowKonamiGif(false), 5000);
  };

  useKonamiCode(triggerKonamiSurprise);

  return (
    <div>
      {showKonamiGif && (
        <div
          style={{
            position: "fixed",
            top: 0,
            left: 0,
            width: "100%",
            height: "100%",
            backgroundColor: "rgba(128, 128, 128, 0.5)", // Light grey overlay
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            zIndex: 1000,
          }}
          onClick={() => setShowKonamiGif(false)}
        >
          <div
            style={{
              textAlign: "center",
              position: "relative",
              animation: "fadeIn 2s forwards",
            }}
          >
            <h1 style={{ animation: "dropIn 1s forwards" }}>
              You Found The ESP Secret!! 
            </h1>
            <img
              src={KonamiGif}
              alt="Konami Surprise"
              style={{ maxWidth: "90%", maxHeight: "90%" }}
            />
          </div>
        </div>
      )}
      <div className="header">
        <div className="logo">
          <img src={espressiflogo1} alt="Company Logo" />
          {/* <h1>Espressif Power Profiler</h1> */}
        </div>
      </div>
      <div className="content">
        <StateContext.Provider value={{ formState, setFormState }}>
          <Navbar
            onGraphDataFetch={handleGraphDataFetch}
            onChipDetailChange={handleChipDetailChange}
          />
          <div style={{ flex: 1, padding: "20px" }}>
            <div className="app-container">
              <ChipDetails chipDetail={chipDetail} />
              <div
                style={{
                  display: "flex",
                  flexDirection: "column",
                  padding: "15px",
                  border: "1px solid #ddd",
                  borderRadius: "8px",
                }}
              >
                <div>
                  <strong>Firmware Bootup time:</strong> 710ms
                </div>
                <div>
                  <strong>Average current firmware bootup:</strong> 20mA
                </div>
              </div>
              <div className="section-wrapper">
                <div className="subsection-title">
                  Power Consumption of an Advertising Interval Beacon
                </div>
                <hr className="section-divider" />
                <BarChartWithD3 data={graphData} />
              </div>
              <div className="section-wrapper">
                <div className="subsection-title">
                  Net Change of consumption values
                </div>
                <hr className="section-divider" />
                <ChipDetailsIndicators
                  chipDetail={chipDetail}
                  prevChipDetail={prevChipDetail}
                />
              </div>
              <div className="section-wrapper">
                <div className="subsection-title">
                  Power Consumption of successive Advertising Interval
                </div>
                <hr className="section-divider" />
                <InteractivePlot />
              </div>
            </div>
            <footer class="arco-layout-footer _footer_odg74_1">
              <div>
                Copyright  2024 Espressif Systems. All rights reserved.
              </div>
            </footer>
          </div>
        </StateContext.Provider>
      </div>
    </div>
  );
}

export default App;
